/* landing.css */

/* Apply specific styling for the landing page */
.landing-container {
    background-color: #fff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    text-align: center;
    max-width: 600px;
    margin: 0 auto;
  }
  
  .landing-title {
    font-size: 36px;
    color: #007bff;
    margin-bottom: 20px;
  }
  
  .landing-description {
    font-size: 18px;
    color: #333;
    margin-bottom: 20px;
  }
  
  .landing-buttons {
    display: flex;
    justify-content: center;
  }
  
  .landing-button {
    background-color: #28a745;
    color: #fff;
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    margin: 0 10px;
    text-decoration: none;
    cursor: pointer;
  }
  
  .landing-button:hover {
    background-color: #218838;
  }